"use client"

import { useEffect } from "react"
import { useRouter } from "next/navigation"
import { useAuth } from "@/context/auth-context"
import DashboardLayout from "@/components/dashboard-layout"
import { Card, CardHeader, CardTitle, CardContent } from "@/components/ui/card"
import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip, Legend } from "recharts"
import DashboardItemsMap from "@/components/dashboard-items-map"

export default function DashboardPage() {
  const { user, isLoading, items, users } = useAuth()
  const router = useRouter()

  useEffect(() => {
    if (!isLoading && !user) {
      router.push("/")
    }
  }, [user, isLoading, router])

  if (isLoading || !user) {
    return <div className="flex items-center justify-center min-h-screen bg-background" />
  }

  // Data untuk card dan chart
  const totalItem = items.length
  const normalCount = items.filter((i) => i.statusMesin === "Normal").length
  const maintenanceCount = items.filter((i) => i.statusMesin === "Maintenance").length
  const rusakCount = items.filter((i) => i.statusMesin === "Rusak").length
  const tidakAktifCount = items.filter((i) => i.statusMesin === "Tidak Aktif").length
  const standbyCount = items.filter((i) => i.statusMesin === "Standby").length
  const chartData = [
    { name: "Normal", value: normalCount },
    { name: "Maintenance", value: maintenanceCount },
    { name: "Rusak", value: rusakCount },
    { name: "Tidak Aktif", value: tidakAktifCount },
    { name: "Standby", value: standbyCount },
  ]
  const COLORS = ["#22c55e", "#eab308", "#ef4444", "#6b7280", "#3b82f6"]

  // Statistik tambahan
  const uniqueCategories = Array.from(new Set(items.map((i) => i.category))).length
  const uniqueLocations = Array.from(new Set(items.map((i) => i.location))).length
  const now = new Date()
  const itemsThisMonth = items.filter((i) => {
    const created = new Date(i.createdAt)
    return created.getMonth() === now.getMonth() && created.getFullYear() === now.getFullYear()
  }).length

  // Recent activity (5 terakhir)
  const recentItems = [...items]
    .sort((a, b) => new Date(b.updatedAt).getTime() - new Date(a.updatedAt).getTime())
    .slice(0, 5)

  // Helper untuk ambil nama user pembuat item
  const getCreatorName = (userId: string) => {
    const creator = users.find((u) => u.id === userId)
    return creator?.name || "Tidak diketahui"
  }

  return (
    <DashboardLayout>
      <div className="py-8 px-2 md:px-8 space-y-8">
        {/* Welcome Card */}
        <div className="flex flex-col md:flex-row md:items-center md:justify-between gap-4">
          <div className="flex items-center gap-4 bg-white rounded-xl border shadow-sm px-6 py-4">
            <div className="w-14 h-14 rounded-full bg-primary flex items-center justify-center text-2xl text-primary-foreground font-bold">
              {user?.name?.charAt(0).toUpperCase()}
            </div>
            <div>
              <div className="text-lg font-semibold text-foreground mb-1">Selamat datang, {user?.name || "Pengguna"}!</div>
              <div className="text-sm text-muted-foreground capitalize">Role: {user?.role}</div>
            </div>
          </div>
          <button
            onClick={() => window.location.href = "/dashboard/items"}
            className="bg-primary text-primary-foreground px-6 py-3 rounded-lg hover:bg-primary/90 transition-colors font-semibold shadow"
          >
            + Tambah Item Baru
          </button>
        </div>

        {/* Statistik Card */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
          <Card>
            <CardHeader>
              <CardTitle>Total Item</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-primary">{totalItem}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle>Kategori</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-blue-600">{uniqueCategories}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle>Lokasi</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-yellow-600">{uniqueLocations}</div>
            </CardContent>
          </Card>
          <Card>
            <CardHeader>
              <CardTitle>Item Baru Bulan Ini</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="text-4xl font-bold text-green-600">{itemsThisMonth}</div>
            </CardContent>
          </Card>
        </div>

        {/* Chart & Recent Activity */}
        <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
          <div className="bg-white rounded-xl border p-6 shadow-sm">
            <h2 className="text-xl font-semibold mb-4">Statistik Status Item</h2>
            <div className="flex items-center gap-4 mb-2">
              <span className="flex items-center gap-1 text-sm"><span className="inline-block w-3 h-3 rounded-full bg-green-500"></span>Normal</span>
              <span className="flex items-center gap-1 text-sm"><span className="inline-block w-3 h-3 rounded-full bg-yellow-400"></span>Maintenance</span>
              <span className="flex items-center gap-1 text-sm"><span className="inline-block w-3 h-3 rounded-full bg-red-500"></span>Rusak</span>
              <span className="flex items-center gap-1 text-sm"><span className="inline-block w-3 h-3 rounded-full bg-gray-500"></span>Tidak Aktif</span>
              <span className="flex items-center gap-1 text-sm"><span className="inline-block w-3 h-3 rounded-full bg-blue-500"></span>Standby</span>
            </div>
            <ResponsiveContainer width="100%" height={160}>
              <PieChart>
                <Pie
                  data={chartData}
                  dataKey="value"
                  nameKey="name"
                  cx="50%"
                  cy="60%"
                  outerRadius={55}
                  label
                >
                  {chartData.map((entry, index) => (
                    <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]} />
                  ))}
                </Pie>
                <Tooltip />
              </PieChart>
            </ResponsiveContainer>
          </div>
          <div className="bg-white rounded-xl border p-6 shadow-sm">
            <h2 className="text-xl font-semibold mb-4">Aktivitas Terbaru</h2>
            {recentItems.length === 0 ? (
              <p className="text-muted-foreground">Belum ada aktivitas item.</p>
            ) : (
              <ul className="divide-y divide-border">
                {recentItems.map((item) => (
                  <li key={item.id} className="py-3 flex flex-col md:flex-row md:items-center md:justify-between gap-2">
                    <div>
                      <span className="font-semibold text-foreground">{item.name}</span>
                      <span className="ml-2 text-xs text-muted-foreground">({item.category})</span>
                      <span className="ml-2 text-xs text-blue-600">dibuat oleh: {getCreatorName(item.createdBy)}</span>
                    </div>
                    <div className="flex items-center gap-3">
                      <span className={`px-2 py-1 rounded text-xs font-semibold ${
                        item.statusMesin === "Normal" ? "bg-green-100 text-green-700" :
                        item.statusMesin === "Maintenance" ? "bg-yellow-100 text-yellow-700" :
                        item.statusMesin === "Rusak" ? "bg-red-100 text-red-700" :
                        item.statusMesin === "Standby" ? "bg-blue-100 text-blue-700" :
                        item.statusMesin === "Tidak Aktif" ? "bg-gray-100 text-gray-700" :
                        "bg-gray-100 text-gray-700"
                      }`}>
                        {item.statusMesin || "-"}
                      </span>
                      <span className="text-xs text-muted-foreground">
                        {new Date(item.updatedAt).toLocaleDateString("id-ID")}
                      </span>
                    </div>
                  </li>
                ))}
              </ul>
            )}
          </div>
        </div>

        {/* Tips/Info */}
        <DashboardItemsMap />
        <div className="bg-blue-50 border border-blue-200 rounded-xl p-4 text-blue-900 text-sm flex items-center gap-3">
          <svg className="w-6 h-6 text-blue-400" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M12 20a8 8 0 100-16 8 8 0 000 16z" /></svg>
          <span>Tips: Gunakan fitur pencarian dan filter untuk menemukan item lebih cepat. Klik nama item di aktivitas untuk melihat detail.</span>
        </div>
      </div>
    </DashboardLayout>
  )
}
