"use client"

import { useAuth } from "@/context/auth-context"
import { MapContainer, TileLayer, Marker, Popup, useMap } from "react-leaflet"
import "leaflet/dist/leaflet.css"
import L from "leaflet"
import { useEffect } from "react"

// Component to handle map bounds
function MapController({ markers }: { markers: any[] }) {
  const map = useMap()

  useEffect(() => {
    if (markers.length > 0) {
      const bounds = L.latLngBounds(markers.map(m => [m.latitude, m.longitude]))
      map.fitBounds(bounds, { padding: [50, 50] })
    }
  }, [markers, map])

  return null
}

const createCustomIcon = (category: string, status: string) => {
  let colorClass = "bg-[#1E293B]" // Default slate
  let ringClass = "ring-[#1E293B]"
  let icon = `<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" class="w-5 h-5 text-white"><path fill-rule="evenodd" d="M11.54 22.351l.07.04.028.016a.76.76 0 00.723 0l.028-.015.071-.041a16.975 16.975 0 001.144-.742 19.58 19.58 0 002.683-2.282c1.944-1.99 3.963-4.98 3.963-8.827a8.25 8.25 0 00-16.5 0c0 3.846 2.02 6.837 3.963 8.827a19.58 19.58 0 002.682 2.282 16.975 16.975 0 001.145.742zM12 13.5a3 3 0 100-6 3 3 0 000 6z" clip-rule="evenodd" /></svg>`

  // Status-based coloring
  if (status === "Rusak" || status === "Critical") {
    colorClass = "bg-red-500"
    ringClass = "ring-red-500"
  } else if (status === "Maintenance" || status === "Standby") {
    colorClass = "bg-orange-500"
    ringClass = "ring-orange-500"
  } else if (status === "aktif" || status === "Normal") {
    colorClass = "bg-emerald-500"
    ringClass = "ring-emerald-500"
  }

  // Category-specific icons (optional, keeping generic pin for now but with different backgrounds)

  return L.divIcon({
    className: "custom-marker",
    html: `
      <div class="relative">
        <div class="absolute -inset-2 rounded-full ${colorClass} opacity-20 animate-ping"></div>
        <div class="relative w-10 h-10 rounded-full ${colorClass} shadow-lg border-2 border-white flex items-center justify-center transform transition-transform hover:scale-110">
          ${icon}
        </div>
        <div class="absolute -bottom-1 left-1/2 -translate-x-1/2 w-1 h-3 bg-black/20 rounded-full blur-[2px]"></div>
      </div>
    `,
    iconSize: [40, 40],
    iconAnchor: [20, 40],
    popupAnchor: [0, -40],
  })
}

export default function DashboardItemsMap() {
  const { items } = useAuth()
  const markers = items.filter((i) => i.latitude && i.longitude)
  // Center map fallback
  const center: [number, number] = markers.length > 0 ? [markers[0].latitude, markers[0].longitude] : [-1.274373, 116.856972]

  return (
    <div className="bg-white rounded-2xl border border-slate-100 shadow-xl overflow-hidden mt-8">
      <div className="p-6 border-b border-slate-100 flex items-center justify-between bg-slate-50/50">
        <div>
          <h2 className="text-xl font-bold text-[#1E293B]">Peta Sebaran Aset</h2>
          <p className="text-slate-500 text-sm mt-1">Real-time monitoring lokasi dan status aset</p>
        </div>
        <div className="flex gap-4 text-xs font-medium">
          <div className="flex items-center gap-2">
            <span className="w-2.5 h-2.5 rounded-full bg-emerald-500"></span> Normal
          </div>
          <div className="flex items-center gap-2">
            <span className="w-2.5 h-2.5 rounded-full bg-orange-500"></span> Maintenance
          </div>
          <div className="flex items-center gap-2">
            <span className="w-2.5 h-2.5 rounded-full bg-red-500"></span> Critical
          </div>
        </div>
      </div>

      <div className="w-full h-[500px] relative z-0">
        <MapContainer
          center={center}
          zoom={15}
          scrollWheelZoom={true}
          style={{ width: "100%", height: "100%" }}
          className="z-0"
        >
          {/* CartoDB Voyager Tiles (Premium Look) */}
          <TileLayer
            url="https://{s}.basemaps.cartocdn.com/rastertiles/voyager/{z}/{x}/{y}{r}.png"
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors &copy; <a href="https://carto.com/attributions">CARTO</a>'
          />

          <MapController markers={markers} />

          {markers.map((item) => (
            <Marker
              key={item.id}
              position={[item.latitude, item.longitude]}
              icon={createCustomIcon(item.category, item.statusMesin || item.status)}
            >
              <Popup className="custom-popup">
                <div className="p-1 min-w-[200px]">
                  <div className="flex items-center gap-3 mb-3">
                    <div className={`w-10 h-10 rounded-lg flex items-center justify-center ${(item.statusMesin === "Rusak" || item.tingkatPrioritas === "Critical") ? "bg-red-50 text-red-600" :
                        (item.statusMesin === "Maintenance") ? "bg-orange-50 text-orange-600" :
                          "bg-emerald-50 text-emerald-600"
                      }`}>
                      <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="currentColor" className="w-5 h-5">
                        <path fillRule="evenodd" d="M3 6a3 3 0 013-3h12a3 3 0 013 3v12a3 3 0 01-3 3H6a3 3 0 01-3-3V6zm14.25 6a.75.75 0 01-.22.53l-2.25 2.25a.75.75 0 11-1.06-1.06L15.44 12l-1.72-1.72a.75.75 0 111.06-1.06l2.25 2.25c.141.14.22.331.22.53zm-9 0a.75.75 0 01.22.53l2.25 2.25a.75.75 0 11-1.06 1.06L9.44 12l1.72-1.72a.75.75 0 11-1.06-1.06L7.85 11.47a.75.75 0 01-.22.53z" clipRule="evenodd" />
                      </svg>
                    </div>
                    <div>
                      <div className="text-xs text-slate-500 font-medium mb-0.5">{item.assetId || "No ID"}</div>
                      <div className="font-bold text-slate-800 text-sm leading-tight">{item.name}</div>
                    </div>
                  </div>

                  <div className="space-y-2 mb-3">
                    <div className="flex justify-between items-center text-xs">
                      <span className="text-slate-500">Status</span>
                      <span className={`font-semibold px-2 py-0.5 rounded-full ${(item.statusMesin === "Rusak") ? "bg-red-100 text-red-700" :
                          (item.statusMesin === "Maintenance") ? "bg-orange-100 text-orange-700" :
                            "bg-emerald-100 text-emerald-700"
                        }`}>
                        {item.statusMesin || "Active"}
                      </span>
                    </div>
                    <div className="flex justify-between items-center text-xs">
                      <span className="text-slate-500">Lokasi</span>
                      <span className="font-medium text-slate-700 truncate max-w-[120px]">{item.location}</span>
                    </div>
                  </div>

                  <a
                    href={`/dashboard/items/${item.id}`}
                    className="block w-full text-center bg-slate-900 hover:bg-slate-800 text-white text-xs font-semibold py-2 rounded-lg transition-colors"
                  >
                    Lihat Detail
                  </a>
                </div>
              </Popup>
            </Marker>
          ))}
        </MapContainer>
      </div>
    </div>
  )
}
